import React, { useState } from 'react';
import trashIcon from '../images/trash.svg'
import ImageModal from './ImageModal';

function Card({ image, title, likes, handleCardClick }) {
  const handleClick = (e) => handleCardClick(e);

  return (
    <div className="post">
      <img className="post__image" src={image} onClick={handleClick} />
      <button className="button post__delete"><img className="post__delete__image" alt="delete post" src={trashIcon} /></button>
      <div className="post__container">
        <h2 className="title post__title">{title}</h2>
        <div className="info">
          <button className="button post__button button__image"></button>
          <p className="like">{likes}</p>
        </div>
      </div>
    </div>
  );
}

export default Card;
